<template>
  <div class="common-container">
    <Row type="flex" justify="start">
      <i-col span="8">
        <Button @click="addType"></Button>
      </i-col>
    </Row>
    <Divider />
    <Row>
      <i-col span="8" offset="4">
        <Tree :data="treeData" :load-data="loadChild" class="t-left" />
      </i-col>
    </Row>
    <Modal v-model="showAddType" title="" :width="380">
      <Form ref="newTypeForm" :model="newType" label-position="right" :label-width="100" :rules="ruleValidate">
        <FormItem label="" prop="typeIdentify">
          <Select v-model="newType.typeIdentify">
            <Option value="A"></Option>
            <Option value="L"></Option>
          </Select>
        </FormItem>
        <FormItem label="" prop="parentCode">
          <Select v-model="newType.parentCode">
            <Option v-for="parentType in parentTypes" :key="parentType.typeCode" :value="parentType.typeCode">{{parentType.typeDesc}}</Option>
          </Select>
        </FormItem>
        <FormItem label="" prop="typeCode">
          <Tooltip content="" placement="right" :disabled="passed" :always="!passed" style="width: 100%;">
            <Input v-model="newType.typeCode" @on-blur="checkTypeCode" />
          </Tooltip>
        </FormItem>
        <FormItem label="" prop="typeDesc">
          <Input v-model="newType.typeDesc" />
        </FormItem>
      </Form>
      <div slot="footer">
        <Button @click="hideModal"></Button>
        <Button type="primary" @click="doAddType"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import API from '@/js/api'
export default {
  name: 'AssetTypes',
  data() {
    return {
      treeData: [
        {
          title: '',
          loading: false,
          children: []
        },
        {
          title: '',
          loading: false,
          children: []
        }
      ],
      newType: {
        parentCode: '',
        typeCode: '',
        typeDesc: '',
        typeIdentify: 'A'
      },
      passed: true,
      showAddType: false,
      parentTypes: [],
      ruleValidate: {
        typeCode: {
          required: true,
          message: ''
        },
        typeDesc: {
          required: true,
          message: ''
        },
        typeIdentify: {
          required: true,
          message: ''
        }
      }
    }
  },
  methods: {
    addType: function() {
      this.showAddType = true
    },
    hideModal: function() {
      this.showAddType = false
    },
    checkTypeCode: function() {
      API.assetTypeCheck(this.newType.typeCode).then(resp => {
        if (resp.code !== API.CODE_CONST.SUCCESS) {
          this.passed = false
        } else {
          this.passed = true
        }
      })
    },
    doAddType: function() {
      if (!this.passed) {
        return
      }
      this.$refs['newTypeForm'].validate(valid => {
        if (valid) {
          let data = {
            typeCode: this.newType.typeCode,
            typeDesc: this.newType.typeDesc,
            typeIdentify: this.newType.typeIdentify,
            parentCode: this.newType.parentCode
          }
          this.$debug(JSON.stringify(data))
          API.addAssetType(data).then(resp => {
            if (resp.code === API.CODE_CONST.SUCCESS) {
              alert('')
              this.refresh()
              this.newType = {
                typeIdentify: 'A'
              }
              this.showAddType = false
            } else {
              alert(resp.msg)
            }
          })
        }
      })
    },
    convertToTreeNode: function(assetTypes, hasChild) {
      if (assetTypes && assetTypes.length > 0) {
        return assetTypes.map(r => {
          let node = {
            typeCode: r.typeCode,
            title: r.typeDesc,
            id: r.id
          }
          if (hasChild) {
            node.loading = false
            node.children = []
          }
          return node
        })
      } else {
        return []
      }
    },
    deleteType: function(typeId) {
      let data = {
        id: typeId
      }
      this.$debug('request:' + JSON.stringify(data))
    },
    refresh: function() {
      API.getAssetParents().then(resp => {
        if (resp.code === API.CODE_CONST.SUCCESS) {
          this.parentTypes = resp.assetTypes
        }
      })
      this.treeData = [
        {
          title: '',
          loading: false,
          children: []
        },
        {
          title: '',
          loading: false,
          children: []
        }
      ]
    },
    loadChild: function(item, callback) {
      this.$log('' + JSON.stringify(item))
      if (item.id) {
        API.getChildByParent({
          id: item.id
        }).then(resp => {
          if (resp.code === API.CODE_CONST.SUCCESS) {
            callback(this.convertToTreeNode(resp.assetTypes))
          }
        })
      } else if (item.title === '') {
        API.getLiabilityParents().then(resp => {
          if (resp.code === API.CODE_CONST.SUCCESS) {
            callback(this.convertToTreeNode(resp.assetTypes, true))
          }
        })
      } else if (item.title === '') {
        API.getAssetParents().then(resp => {
          if (resp.code === API.CODE_CONST.SUCCESS) {
            callback(this.convertToTreeNode(resp.assetTypes, true))
          }
        })
      }
    }
  },
  mounted() {
    this.refresh()
  },
  computed: {
    typeIdentify: function() {
      return this.newType.typeIdentify
    }
  },
  watch: {
    typeIdentify: function(newVal, o) {
      if (newVal === 'L') {
        API.getLiabilityParents().then(resp => {
          if (resp.code === API.CODE_CONST.SUCCESS) {
            this.parentTypes = resp.assetTypes
          }
        })
      } else if (newVal === 'A') {
        API.getAssetParents().then(resp => {
          if (resp.code === API.CODE_CONST.SUCCESS) {
            this.parentTypes = resp.assetTypes
          }
        })
      }
    }
  }
}
</script>

<style scoped>
.t-left {
  text-align: left;
}
</style>