<template>
  <Modal v-model="innerShowAddModal" title="" :width="380">
      <Form ref="newRecord" :model="newRecord" label-position="right" :label-width="100" :rules="ruleValidate">
        <FormItem label=":" prop="createTime">
          <DatePicker type="datetime" v-model="newRecord.createTime" clearable />
        </FormItem>
        <FormItem label=":" prop="location">
          <Input type="text" v-model="newRecord.location" />
        </FormItem>
        <FormItem label=":" prop="money">
          <NumberInput v-model="newRecord.money" placeholder="" />
        </FormItem>
        <FormItem label=":" prop="target">
          <Input type="text" v-model="newRecord.target" />
        </FormItem>
        <FormItem label=":" prop="memo">
          <Input type="text" v-model="newRecord.memo" />
        </FormItem>
        <FormItem label=":" prop="orderType">
          <Input type="text" v-model="newRecord.orderType" />
        </FormItem>
        <FormItem label="/:" prop="inOutType">
          <Select v-model="newRecord.inOutType">
            <Option value=""></Option>
            <Option value=""></Option>
          </Select>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button @click="hideModal"></Button>
        <Button type="primary" @click="doAddCostRecord"></Button>
      </div>
    </Modal>
</template>

<script>

import API from '@/js/api.js'
import NumberInput from '@/components/common/NumberInput.vue'

export default {
  name: "CostRecordAdder",
  components: {
    NumberInput
  },
  model: {
    prop: 'showAddModal',
    event: 'change'
  },
  props: {
    showAddModal: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      innerShowAddModal: this.showAddModal,
      newRecord: {},
      ruleValidate: {
        createTime: {
          required: true,
          message: ''
        },
        money: {
          required: true,
          message: ''
        },
        target: {
          required: true,
          message: ''
        },
        inOutType: {
          required: true,
          message: ''
        }
      },
      costRecordColumns: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '',
          sortable: 'custom',
          key: 'createTime',
          minWidth: 150
        },
        {
          title: '',
          sortable: 'custom',
          key: 'money',
          minWidth: 80
        },
        {
          title: '',
          key: 'goodsName',
          minWidth: 150
        },
        {
          title: '',
          key: 'inOutType',
          minWidth: 110,
          align: 'center',
          filters: [
            {
              label: '',
              value: ''
            },
            {
              label: '',
              value: ''
            }
          ],
          filteredValue: [''],
          filterMultiple: false,
          filterRemote: function (value, row) {
            this.inOutType = value[0]
            this.pageNo = 0
            this.query()
          }
        },
        {
          title: '',
          key: 'isDeleted',
          minWidth: 110,
          align: 'center',
          filters: [
            {
              label: '',
              value: 1
            },
            {
              label: '',
              value: 0
            }
          ],
          filteredValue: [0],
          filterMultiple: false,
          filterRemote: function (value, row) {
            this.isDeleted = value[0]
            this.pageNo = 0
            this.query()
          }
        },
        {
          title: '',
          key: 'isHidden',
          minWidth: 110,
          align: 'center',
          filters: [
            {
              label: '',
              value: 0
            },
            {
              label: '',
              value: 1
            }
          ],
          filteredValue: [0],
          filterMultiple: false,
          filterRemote: function (value, row) {
            this.isHidden = value[0]
            this.pageNo = 0
            this.query()
          }
        },
        {
          title: '',
          key: 'target',
          minWidth: 150
        },
        {
          title: '',
          key: 'orderType',
          minWidth: 150
        },
        {
          title: '',
          key: 'location',
          minWidth: 150
        }
      ]
    }
  },
  watch: {
    showAddModal: function (n, v) {
      this.innerShowAddModal = n
    },
    innerShowAddModal: function (n, v) {
      this.$emit('change', n)
    }
  },
  methods: {
    hideModal: function () {
      this.$emit('hide-modal')
    },
    doAddCostRecord: function () {
      this.$debug('data:' + JSON.stringify(this.newRecord))
      this.$refs['newRecord'].validate(valid => {
        if (valid) {
          let request = {
            createTime: this.dateFormat(this.newRecord.createTime, 'yyyy-MM-dd HH:mm:ss'),
            inOutType: this.newRecord.inOutType,
            money: this.newRecord.money,
            target: this.newRecord.target,
            location: this.newRecord.location,
            memo: this.newRecord.memo,
            orderType: this.newRecord.orderType,
            goodsName: this.newRecord.goodsName
          }
          API.addRecord(request).then(resp => {
            if (resp.code === API.CODE_CONST.SUCCESS) {
              this.hideModal()
              this.$Message.success('')
              this.$emit('requery-list')
              this.newRecord = {}
              this.innerShowAddModal = false
            }
          })
        } else {
          this.$Message.error('')
        }
      })
    }
  }
}
</script>