<template>
  <div class="container">
    <div id="demo-charts" style="height: 400px;width:600px;" />
  </div>
</template>

<script>
import echarts from 'echarts'
export default {
  name: 'echarts-demo',
  data() {
    return {}
  },
  methods: {
    initEcharts: function() {
      this.$debug('echarts')
      var myChart = echarts.init(document.getElementById('demo-charts'))
      // 
      myChart.setOption({
        title: {
          text: 'ECharts '
        },
        tooltip: {},
        xAxis: {
          data: ['', '', '', '', '', '']
        },
        yAxis: {},
        series: [
          {
            name: '',
            type: 'bar',
            data: [5, 20, 36, 10, 10, 20]
          }
        ]
      })
    }
  },
  mounted() {
    this.initEcharts()
  }
}
</script>
